<template>
  <div>
    <!-- <mu-appbar style="width: 100%;" class="header1 backgroundNaviBar">
      <div slot="left" color="#ffffff">
        <a @click="()=>{
          $router.go(-1);
          }">
          <mu-button icon slot="left" class="icon" size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>

      <div style="width:100%;text-align:center;" >
        <span style="font-size:18px;color:white;">创建点位</span>
      </div>
      <div slot="right" class="icon" size="20">
        <mu-button icon slot="right" class="icon" size="20" @click="queryBtn">
          <mu-icon color="white" value="add"></mu-icon>
        </mu-button>
      </div>
    </mu-appbar>-->
    <van-nav-bar
      :fixed="true"
      title="创建点位"
      @click-left="()=>{
          $router.go(-1)
         }"
      @click-right="queryBtn"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <van-icon name="add" slot="right" size="20" />
    </van-nav-bar>
    <div class="content">
      <div class="contHeader">
        <van-swipe-cell v-for="(item,index) in TaskList" :key="index" class="borderBottom">
          <!-- <van-cell
            :border="false"
            class="borderBottom"
            @click="btnNextNavigate(item.pollutantName,item.routingName,item.id,carId)"
            :title="'名称：'+item.pollutantName"
            style="border-radius: 10px;"
          />-->

          <!-- 点击事件 -->
          <!--@click="btnNextNavigate(item.pollutantName,item.routingName,item.id,carId)"  -->
          <van-cell-group
            style="border-radius: 10px;"
            @click="btnNextNavigate(item.pollutantName,item.routingName,item.id,carId)"
          >
            <van-cell :title="'名称：'+item.pollutantName" :label="item.gmtCreate" :border="false" />
          </van-cell-group>
          <template slot="right">
            <van-button
              @click="closeThis(index,item.id)"
              square
              type="danger"
              text="删除"
              style="height:65px;"
            />
          </template>
        </van-swipe-cell>
      </div>
    </div>

    <!-- <div class="bottomBtn" @click="endTask">
      <span>结束当前任务</span>
    </div>-->
    <div class="btnSubmitTask">
      <div class="borderCircle" @click="endTask">
        <span>结束当前任务</span>
      </div>
    </div>
    <!-- <Add /> -->
    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list>
          <div class="containerMain">
            <div class="flexContainer">
              <div class="titleselect">
                <span>点位名称：</span>
              </div>
              <div class="select" id="classPicstation" @click="selectDataPicStation">
                <div style="flex: 1;">
                  <input
                    type="text"
                    placeholder="请输入当前的站点名称"
                    class="input"
                    disabled
                    v-model="station"
                  />
                </div>
                <div class="imageSize">
                  <img src="static/img/toBottom.png" alt />
                </div>
              </div>
            </div>
          </div>
          <van-radio-group v-model="radio" v-for="(item,index) in checkData" :key="index">
            <van-cell-group>
              <van-cell :title="item.gmtCreate" clickable @click="radio = item.id">
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
              <!-- <van-cell title="单选框 2" clickable @click="radio = '2'">
                <van-radio slot="right-icon" name="2" />
              </van-cell>-->
            </van-cell-group>
          </van-radio-group>
        </mu-list>
        <van-button
          class="backgroundNaviBar"
          :color="$store.state.color"
          size="large"
          @click="addPointFunc"
        >提交点位</van-button>
      </mu-bottom-sheet>
    </mu-container>
    <van-dialog v-model="show" class="titleColor" title="请手动输入污染源名称" show-cancel-button>
      <div class="select centerBorder" style="height:60px;">
        <input
          type="text"
          class="input border"
          placeholder="请输入您的污染源名称"
          v-model="station"
          style="width:80%"
        />
      </div>
    </van-dialog>
  </div>
</template>
<script>
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
import { Dialog } from "vant";
// import { Dialog } from "vant";
Vue.use(Dialog);
// import Vue from 'vue';
import AppBace from "../../../static/ajax";
import Add from "../../views/addPoint/index";
export default {
  data() {
    return {
      TaskList: [],
      carId: "",
      timeOutEvent: 0,
      lng: "",
      lat: "",
      open: false,
      show: false,
      station: "",
      radio: "",
      checkData: [],
      routingName: ""
    };
  },
  // components: {
  //   Add
  // },
  methods: {
    queryBtn() {
      this.open = true;
    },
    // 子组件调用父组件方法
    openPoint() {
      this.open = true;
    },
    // 新增点位
    addPointFunc() {
      this.open = false;
      var _this = this;
      if (_this.radio === "") {
        Toast("当前数据为空，无法创建点位数据！");
      } else {
        var url = "/mobile/point";
        this.$http
          .post(url, {
            pollutantName: _this.station,
            routingName: _this.$store.state.routingName,
            dataId: _this.radio
          })
          .then(res => {
            console.log(res);
            _this.checkStatus();
            _this.radio = "";
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    selectDataPicStation() {
      this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicstation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.enterpriseNum, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择点位", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          switch (selectArr[0]) {
            case "其他":
              _this.show = true;

              break;

            default:
              _this.station = selectArr[0];
              _this.open = true;
              break;
          }
        }
      });
    },
    getposition(val) {
      var _this = this;

      navigator.vibrate(500);
      var me = {
        msg: ""
      };
      var onSuccess = function(position) {
        console.log("position", position);
        var lat = "";
        var lng = "";
        var wgs84togcj02 = coordtransform.wgs84togcj02(
          Number(position.coords.longitude),
          Number(position.coords.latitude)
        );
        console.log(wgs84togcj02);
        _this.lng = wgs84togcj02[0];
        _this.lat = wgs84togcj02[1];
        _this.getGaoDE(wgs84togcj02[0], wgs84togcj02[1]);
        //   return arr;
      };
      var error = function(error) {
        me.msg =
          "code: " + error.code + "\n" + "message: " + error.message + "\n";

        alert(me.msg);
        console.log(me.msg);
      };

      navigator.geolocation.getCurrentPosition(onSuccess, error, {
        maximumAge: 30000,
        timeout: 30000,
        enableHighAccuracy: true
      });
    },
    getGaoDE(e, f) {
      var geocoder;
      var that = this;
      var lnglatXY = new AMap.LngLat(Number(e), Number(f));
      //加载地理编码插件
      AMap.plugin(["AMap.Geocoder"], function() {
        geocoder = new AMap.Geocoder({
          radius: 1000, //以已知坐标为中心点，radius为半径，返回范围内兴趣点和道路信息
          extensions: "all" //返回地址描述以及附近兴趣点和道路信息，默认"base"
        });
        //返回地理编码结果
        AMap.event.addListener(geocoder, "complete", geocoder_callBack);
        //逆地理编码
        geocoder.getAddress(lnglatXY);
      });
      function geocoder_callBack(params) {
        console.log("params", params);
        that.address = params.regeocode.formattedAddress;
        // alert("当前" + params.regeocode.formattedAddress);
        // alert("状态" + params.info);
        // alert(params.type);
        // alert("数组" + params.regeocode, addressComponent);
      }
    },
    onConfirm() {
      alert(1);
    },
    gtouchstart() {
      // 开始触摸
      this.timeOutEvent = setTimeout(() => {
        // 长按3秒
        this.timeOutEvent = 0;

        // this.$router.push('/result')
      }, 3000);
    },
    gtouchmove() {
      // 看具体需求
      clearTimeout(this.timeOutEvent);
      this.timeOutEvent = 0;
    },
    gtouchend() {
      clearTimeout(this.timeOutEvent);
      this.timeOutEvent = 0;
    },
    async checkStatus() {
      var url = "/mobile/routing/check";
      var res = await AppBace.request(url, "GET", {}, this.token);
      console.log(res);
      this.TaskList = res.data.data.monitoringDataList;
      this.carId = res.data.data.vehicleId;
    },
    async endTask() {
      var _this = this;
      if (this.TaskList.length === 0) {
        // Toast("当前点位数据为空，请按时完成您本次任务！");
        Dialog.confirm({
          title: "删除路线提示",
          message: "当前路线点位为空，我们会帮你删除当前路线"
        })
          .then(() => {
            // on confirm
            var url = "/mobile/routing";
            var routingName = this.$store.state.routingName;
            var param = { routingName: routingName };
            // var res = await AppBace.Deletedata(url,"DELETE",{routingName},this.token);
            this.$http
              .delete(url, {
                params: param
              })
              .then(res => {
                console.log(res);
                _this.$store.state.taskStatus = 0;
                if (res.data.message === "success") {
                  Toast("当前路线已经删除,请返回重新创建路线");
                  _this.$router.go(-2);
                }
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(() => {
            Toast("路线还在,您继续进行您的任务！");
          });
      } else {
        var _this = this;

        var routingName = this.$store.state.routingName;
        var url = "/mobile/routing/finish?routingName=" + routingName;
        // var url = "/mobile/routing/finish";
        // var res = await AppBace.request(
        //   url,
        //   "POST",
        //   { routingName },
        //   this.token
        // );
        // console.log(res);
        this.$http
          .post(url, {
            routingName: routingName
          })
          .then(res => {
            console.log(res);

            _this.$router.go(-2);
            _this.$store.commit("pointDataList", []);
            Toast("任务已经完成");
            _this.$store.state.taskStatus = 0;
          })
          .catch(err => {
            console.log(err);
            Toast("任务结束失败");
          });
        // debugger;
      }
    },
    btnNextNavigate(value, name, id, taskId) {
      console.log(value, id);
      this.station = value;
      this.routingName = name;
      this.radio = id;
      // Toast("点击对" + value + "点位进行编辑");
      // this.$router.push({
      //   path: "/stationEdit",
      //   query: {
      //     name: name,
      //     id: id,
      //     taskId: taskId
      //   }
      // });
      this.open = true;
    },
    closeThis(index, id) {
      console.log(index);
      var _this = this;
      Dialog.confirm({
        title: "",
        message: "确认删除当前点位？"
      })
        .then(() => {
          // on confirm
          console.log(index);
          _this.TaskList.splice(index, 1);
          _this.initDeleteData(id);
        })
        .catch(() => {
          // on cancel
        });
    },
    AddStation() {
      Toast("新增点位");
      console.log(this.$store.state);
      this.$router.push({
        path: "/stationEdit",
        query: {}
      });
    },
    async initDeleteData(Id) {
      var url = "/mobile/point";
      var id = Id;
      var param = { id: Id };
      // var res = await AppBace.Deletedata(url,"DELETE",{routingName},this.token);
      this.$http
        .delete(url, {
          params: param
        })
        .then(res => {
          console.log(res);
          // _this.status = 0;
          if (res.data.message === "success") {
            Toast("当前点位已经删除,请返回重新创建点位");
          }
        })
        .catch(err => {
          console.log(err);
        });

      // var res = await AppBace.Deletedata(url, "DELETE", { id }, this.token);
      // console.log(res);
    },
    submitData() {
      var _this = this;
      this.$http
        .post(url, {
          pollutantName: _this.station,
          routingName: _this.routingName,
          dataId: _this.radio
        })
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    async initData() {
      var _this = this;
      var url = "/mobile/point/data";
      var vehicleId = this.$store.state.vehicleIdStatus;
      var res = await AppBace.request(
        url,
        "GET",
        {
          vehicleId
        },
        this.token
      );
      console.log(res.data.data);
      _this.checkData = res.data.data;
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    // this.TaskList = this.$store.state.pointName;
    this.checkStatus();
    this.initData();
    //  debugger
  }
};
</script>
<style scoped>
@import "../../../static/css/maintain.css";
@import "../../../static/css/model.css";
.content {
  margin-top: 46px;
  padding: 10px 5px;
}
.header1 {
  position: fixed;
  top: 0;
  width: 100%;
}
.icon img {
  width: 24px;
  height: 24px;
}
.borderBottom {
  border-bottom: 1px solid rgb(241, 240, 240);
}
.imgPosition {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  background-color: white;
}
.bottomBtn {
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f7ae84;
  color: white;
  box-shadow: 2px 2px 2px #ccc;
}
.listStyle {
  border-bottom: 1px solid #ccc;
  /* box-shadow: 5px 5px 5px #ccc; */
}
.contHeader {
  border-radius: 10px;
  padding: 10px;
  background: white;
}
.btnSubmitTask {
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-color: white; */
  margin-top: 40px;
  color: white;
}
.borderCircle {
  height: 120px;
  width: 120px;
  /* background: #24d7c1; */
  /* background: linear-gradient(#28dcbf, #12c6c7); */
  background: linear-gradient(#337dff, #23b9ff);

  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
}
.containerMain {
  border-bottom: 10px solid rgb(228, 228, 228);
  border-radius: 0;
}
.imageSize img {
  width: 14px;
  height: 14px;
}
.input {
  border: none;
  height: 38px;
  border-radius: 5px;
  /* border:1px #ccc solid; */
}
#classPicstation {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border: 1px #ccc solid;
  height: 40px;
  border-radius: 5px;
  /* padding: 1px; */
}
.imageSize {
  display: flex;
  align-items: center;
  margin-right: 10px;
}
.border {
  border: 1px #ccc solid;
  font-size: 12px;
  color: rgb(95, 95, 95);
}
.titleColor {
  color: blue;
}
</style>


